body {
  background-image: url(./imagens//17203830078315.webp)
}

h1 {
  display: flex;
  justify-content: center;
  font-family: "Lucida Console", Monaco, monospace;
  font-size: 55px;
  letter-spacing: -1.4px;
  word-spacing: -2px;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none solid rgb(68, 68, 68);
  font-style: normal;
  font-variant: normal;
  text-transform: uppercase;
  text-shadow: rgb(0, 0, 0) 2px 2px 2px;
}

.solar-system {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.solar-system h2 {
  display: flex;
  justify-content: center;
  font-family: "Lucida Console", Monaco, monospace;
  font-size: 30px;
  letter-spacing: -1.4px;
  word-spacing: -2px;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none solid rgb(68, 68, 68);
  font-style: normal;
  font-variant: normal;
  text-transform: uppercase;
  text-shadow: rgb(0, 0, 0) 2px 2px 2px;
  background: rgba(108, 92, 92, 0.1);
  box-shadow: 0 8px 32px 0 rgba(255, 255, 255, 0.043);
  backdrop-filter: blur( 1px );
  -webkit-backdrop-filter: blur( 1px );
  width: 200px;
  border-radius: 10px;
  border: 1px solid rgba( 255, 255, 255, 0.18 );
}

.planet-cards {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 30px;
}

.planet-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  font-size: 25px;
  color:#FFFFFF;
  width: 250px;
  background: rgba(108, 92, 92, 0.1);
  box-shadow: 0 8px 32px 0 rgba(255, 255, 255, 0.043);
  backdrop-filter: blur( 1px );
  -webkit-backdrop-filter: blur( 1px );
  border-radius: 10px;
  border: 1px solid rgba( 255, 255, 255, 0.18 );
}

.planet-card img{
  width: 200px;
  margin-right: 25px;
  animation-name: floating;
  animation-duration: 7s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  margin-left: 30px;
  margin-top: 5px;
}

.planet-card p {
  font-size: 30px;
}

/* MISSÕES */

.mission-system {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 100px;
}

.mission-system h2 {
  display: flex;
  justify-content: center;
  font-family: "Lucida Console", Monaco, monospace;
  font-size: 30px;
  letter-spacing: -1.4px;
  word-spacing: -2px;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none solid rgb(68, 68, 68);
  font-style: normal;
  font-variant: normal;
  text-transform: uppercase;
  text-shadow: rgb(0, 0, 0) 2px 2px 2px;
  background: rgba(108, 92, 92, 0.1);
  box-shadow: 0 8px 32px 0 rgba(255, 255, 255, 0.043);
  backdrop-filter: blur( 1px );
  -webkit-backdrop-filter: blur( 1px );
  width: 200px;
  border-radius: 10px;
  border: 1px solid rgba( 255, 255, 255, 0.18 );
}

.mission-cards {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 30px;
}

.mission-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color:#FFFFFF;
  line-height: 0;
  width: 500px;
  background: rgba(108, 92, 92, 0.1);
  box-shadow: 0 8px 32px 0 rgba(255, 255, 255, 0.043);
  backdrop-filter: blur( 1px );
  -webkit-backdrop-filter: blur( 1px );
  border-radius: 10px;
  border: 1px solid rgba( 255, 255, 255, 0.18 );
}

.mission-card p {
  font-size: 25px;
  text-shadow: rgb(0, 0, 0) 5px 3px 3px;
}

/* ANIMAÇÕES */

@keyframes floating {
  0% { transform: translate(0,  -0px); }
  50%  { transform: translate(0, 30px); }
  100%   { transform: translate(0, -0px); }   
}